import $ from "jquery";
import React from "react"

export default class Lost extends React.Component{
  componentDidMount(){
    $(window).on("move",()=>{
      $(".ff").each(()=>{
        $(this).left(this.offset("left")+5+"px")
      })
    })
  }
  render(){
    return (
      <div>
        <div className='ff f1'></div>
        <div className='ff f2'></div>
        <div className='ff f3'></div>
        <div className='ff f4'></div>
      </div>
    )
  }
}



